	module.exports = {"data":{"markdownRemark":{"html":"<h1>注意</h1>\n<blockquote>\n<p>目前博客处于开发阶段，更新会很频繁，文章内容也不是精校，仅用于测试。</p>\n</blockquote>\n<h1>About Us</h1>\n<p>我们是来自三个不同城市的闷骚程序员，关于这个 blog 大多是我们自嗨、吐槽、写作、记录的地方，当然如果能够对你有所帮助那自然是极好的。如果你对此感兴趣，欢迎常来。</p>\n<p>光镜，这个名字源于我（leon）很喜欢的一个英剧《黑镜》。</p>\n<p>编剧 Charlie Brooker 是这样描述的：</p>\n<blockquote>\n<p>The \"black mirror\" of the title is the one you'll find on every wall, on every desk, in the palm of every hand: the cold, shiny screen of a TV, a monitor, a smartphone.</p>\n</blockquote>\n<p>但不巧的是，在 github 中黑镜已经被注册占用，于是我就换了光镜这个名字。</p>\n<p>镜子本是了解自己的工具，但是屏幕这块镜子，发出的光遮盖了原本镜中的自己，很多人迷失在其中。只有关闭它才能重新发现真实的自己。</p>\n<h2>Leon</h2>\n<p><code>&#x3C;男/></code> <code>&#x3C;成都/></code> <code>&#x3C;前端攻城狮/></code> <code>&#x3C;刀塔/></code> <code>&#x3C;设计/></code></p>\n<blockquote>\n<p>凡事总须研究，才会明白。古来时常吃人，我也还记得，可是不甚清楚。我翻开历史一查，这历史没有年代，歪歪斜斜的每叶上都写着“ 仁义道德 ”几个字。我横竖睡不着，仔细看了半夜，才从字缝里看出字来，满本都写着两个字是“ 吃人 ”！</p>\n</blockquote>\n<h2>MelodyFrom</h2>\n<p>TP CD 中...</p>\n<h2>ygy4870</h2>\n<p>TP CD 中...</p>\n<h2>骑着蜗牛去爬山</h2>\n<p>TP CD 中...</p>\n<hr>\n<ul>\n<li>Content: Leon、MelodyFrom、ygy4870、骑着蜗牛去爬山</li>\n<li>Design: Leon</li>\n<li>Code: Leon</li>\n<li>Powered: Gatsby</li>\n</ul>","frontmatter":{"date":"March 19, 2018","path":"/about-us","title":"关于我们","author":"Leon","dsct":"基佬四人行","tags":null}}},"pathContext":{}}

